.container {
  width: 98%;
  height: 100%;
  background-color: #adc7e9;
  margin: 0 auto;
  display: flex;
}

.roomList {
  width: 30%;
  min-width: 260px;
  height: 100%;
  background-color: white;
  box-shadow: 0px 1px 3px #8e8e8e;
}

.roomView {
  width: 68%;
  min-width: 400px;
  height: 100%;
  background-color: white;
  box-shadow: 0px 1px 3px #8e8e8e;
  margin-left: 2%;
  overflow-x: auto;
}

